@import url('https://fonts.googleapis.com/css?family=Montserrat:400,500,600,700');
@import url('https://fonts.googleapis.com/css?family=Open+Sans:400,600,700');
@import url('https://fonts.googleapis.com/css?family=Indie+Flower');

@header-bg: #fff;
@header-height: 120px;

@footer-bg: #000;
@footer-height: 271px;

@font-header: 'Montserrat', sans-serif;
@font-header-color: #222;
@font-body: 'Open Sans', sans-serif;
@font-body-color: #888;

@main-color: #ffc200;
@border-color: #eee;
@hover-color: #f9f9f9;

html, body  {
    height: 100%;    
}

.wrapper    {
    position: relative;
    min-height: 100%;
    padding-bottom: @footer-height;
    font-family: @font-body;
    color: @font-body-color;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.h1 {
    @media(max-width: 767px)    {
        font-size: 28px;    
    }
}

.header {
    height: @header-height;    
    background: @header-bg;
    border-bottom: 1px solid @border-color;
    
    .nav-brand, .main-nav  {
        height: @header-height;
    }
    
    .nav-brand  {
        line-height: @header-height;
        float: left;
        font-size: 40px;   
        font-family: 'Indie Flower', cursive;
    }
    
    .main-nav   {
        float: right;  
        
        > li    {
            float: left;   
            
            > a {
                line-height: @header-height - 20;
                display: block;
                padding: 10px 20px;
                color: @font-body-color;
                font-weight: 600;
                text-transform: uppercase;
                
                &:hover, &:focus    {
                    background-color: @hover-color;    
                    color: @font-header-color;
                    text-decoration: none;
                }
            }
        }
    }
}

a   {
    color: @main-color;    
}

.h5 {
    font-size: 16px;
    line-height: 24px;
}

.footer {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    height: @footer-height;    
    background: @footer-bg;
    padding-top: 60px;
    padding-bottom: 60px;
    
    address   {
        line-height: 24px;    
    }
}

.main-slider    {
    position: relative;
    max-height: 600px !important;    
    background-image: url('../img/header1.jpg');
    background-size: cover;
    background-position: center;
    
    &:before, &:after    {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
    }
    
    &:before    {
        z-index: 2;
        background: linear-gradient(110deg, rgba(295,194,0,0.2), rgba(0,166,220,0.2));
    }
    
    &:after {
        background: rgba(0,0,0,0.6);
    }
    
    .container  {
        position: relative;    
        height: 100%;
    }
    
    h1  {
        position: absolute;
        z-index: 2;    
        color: #fff;
        top: 30%;
        left: 0;
        transform: translateY(-30%);
        -webkit-transform: translateY(-30%);
        
        @media(min-width: 992px)   {
            line-height: 70px;
            font-size: 55px;
        }
        
        @media(min-width: 768px) and (max-width: 991px)   {
            line-height: 60px;
            font-size: 45px;
        }
        
        @media(max-width: 767px)    {
            left: 20px;
        }
        
        @media(min-width: 481px) and (max-width: 767px)   {
            line-height: 40px;
            font-size: 30px;
        }
        
        @media(max-width: 480px)    {
            font-size: 22px;
            line-height: 32px;
        }
        
        > div   {
            opacity: 0;
            transition: opacity 0.5s, transform 1s;
            -webkit-transition: opacity 0.5s, -webkit-transform 1s;
            
            &.slide-left    {
                transform: translate(-40px, 0);   
            }
            
            &.slide-right    {
                transform: translate(40px, 0);   
            }
            
            &.slide-up    {
                transform: translate(0, 40px);   
            }
            
            &.active    {
                opacity: 1;    
                transform: translate(0, 0);  
            }
        }
    }
}

.category-group {
    margin-top: 40px;    
}

.category-item  {
    float: left;
    padding: 20px;
    width: 100/3%;
    
    @media(min-width: 992px)    {
         width: 100/3%;    
    }
    
    @media(min-width: 601px) and (max-width: 991px)    {
        width: 50%;   
    }
    
    @media(max-width: 600px)    {
         float: none;
         width: 280px;
         margin-left: auto;
         margin-right: auto;
         padding: 20px 0;
    }
    
    .inner-content  {
        position: relative;
        background-size: cover;     
        width: 100%;
        height: 350px;
        border-radius: 8px;
        box-shadow: 5px 5px 10px rgba(0,0,0,0.5);
        
        > span  {
            display: inline-block;
            position: relative;
            vertical-align: middle;    
            z-index: 2;
            line-height: 40px;
        }
        
        &:before    {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            border-radius: 8px;
            background: rgba(0,0,0,0.5);
        }
        
        &:after {
            content: '';
            display: inline-block;
            height: 100%;
            vertical-align: middle;
        }
    }
}

.bg-black  {
    background-color: #000;
}

.bg-main  {
    background-color: @main-color;
}

.text-main  {
    color: @main-color;    
}

.text-dark  {
    color: @font-header-color;    
}

.text-white  {
    color: #fff;    
}

.font-header    {
    font-family: @font-header;    
}

.pad-lg {
    padding-top: 80px;
    padding-bottom: 80px;
    
    @media(max-width: 767px)    {
        padding-top: 50px;
        padding-bottom: 50px;
    }
}

.pad-md {
    padding-top: 40px;
    padding-bottom: 40px;
    
    @media(max-width: 767px)    {
        padding-top: 25px;
        padding-bottom: 25px;
    }
}

.no-m   {
    margin: 0;    
}

.no-m-t {
    margin-top: 0;    
}

.no-m-b {
    margin-bottom: 0;    
}